import { React } from 'react';
import $ from 'jquery';
import styled from 'styled-components';

var channelID = "UCoKzETf2enGb9eGPXiLZBEA";
var reqURL = "https://www.youtube.com/feeds/videos.xml?channel_id=";
$.getJSON("https://api.rss2json.com/v1/api.json?rss_url=" + encodeURIComponent(reqURL) + channelID, function(data) {
    var link = data.items[0].link;
    var id = link.substr(link.indexOf("=") + 1);
    $("#youtube_video").attr("src", "https://youtube.com/embed/" + id + "?controls=0&showinfo=0&rel=0");
});

const Styles = styled.div`
    @media (max-width: 479px) {
        .ytvideo {
            max-width: 100%;
            height: auto;
    }
}
    @media (min-width: 480px) {
        .ytvideo {
            width: 854px;
            height: 480px;
    }
}

    #youtube_video {
        width: 480px;
        height: 360px;
    }
`; 

export const Home = () => (
    <Styles>
        <div class="home">
            <div class="container-fluid">
                <div class="row">
                    <div class="col-6 pt-5 text-center">
                        <h2>Checkout my Channel</h2><br />
                        <h3>
                            <a href="https://www.youtube.com/c/Kxffie?sub_confirmation=1" target="_blank">youtube.com/Kxffie</a>
                        </h3>

                    </div>
                    <div class="col-6 text-center pt-5">
                        <iframe class="embed-responsive-item" id="youtube_video" frameborder="0" allowfullscreen></iframe>
                    </div>
                </div>
                <div class="row">
                    <div class="col text-center pt-5">
                        not sure what else to put
                    </div>
                </div>
            </div>
        </div>
    </Styles>
)