import React from 'react';
import { Nav, Navbar } from 'react-bootstrap';
import styled from 'styled-components';
import * as Icon from 'akar-icons';
import { DiBootstrap } from 'react-icons/di';
import { FaReact } from 'react-icons/fa';

import logo from '../assets/favicon64.png'

const Styles = styled.div`
    * {
        background-color: #262626;
    }
    .navbar {
        box-shadow: -1px 5px 10px #262626;
        width: 85%;
        border-radius: 0px 0px 15px 15px;
    }
    @media only screen and (max-width: 768px) {
        .navbar {
            width: 100%;
        }
    }
    .navbar-nav, .nav-link {
        font-size: 18px;
    }

    .navbar-brand, .navbar-nav, .nav-link {

        &:hover {
            color: white;
        }
    }
    img {
        border-radius: 50%;
    }
    .noSelect {
        -webkit-touch-callout: none;
        -webkit-user-select: none;
        -khtml-user-select: none;
        -moz-user-select: none;
        -ms-user-select: none;
        user-select: none;
    }
`; 

export const NavigationBar = () => (
    <Styles>
        <Navbar className="container-fluid" expand="md" variant="dark">
            <Navbar.Brand href="/" className="noSelect">
                <img
                    alt=""
                    src= {logo}
                    width="32"
                    height="32"
                    className="d-inline-block align-top"
                />{' '}
                Kxffie Hub
            </Navbar.Brand>
            <Navbar.Toggle aria-controls="basic-navbar-nav"></Navbar.Toggle>
            <Navbar.Collapse id="basic-navbar-nav">
                <Nav className="ml-auto">
                    <Nav.Item><Nav.Link href="/"><Icon.Home size={20} style={{display: "inline", verticalAlign: -4 }} /> Home</Nav.Link></Nav.Item>
                    <Nav.Item><Nav.Link href="/about"><Icon.Info size={20} style={{ display: "inline", verticalAlign: -4 }} /> About</Nav.Link></Nav.Item>
                    <Nav.Item><Nav.Link href="/hire"><Icon.LightBulb size={20} style={{ display: "inline", verticalAlign: -4 }} /> Hire</Nav.Link></Nav.Item>
                    <Nav.Item><Nav.Link href="/contact"><Icon.Phone size={20} style={{ display: "inline", verticalAlign: -4 }} /> Contact</Nav.Link></Nav.Item>
                    <Nav.Item><Nav.Link href="/projects"><Icon.Folder size={20} style={{ display: "inline", verticalAlign: -4 }} /> Projects</Nav.Link></Nav.Item>
                </Nav>
            </Navbar.Collapse>
                <span class="navbar-text float-right d-none d-md-block noSelect">
                    <FaReact size={20} style={{ display: "inline", verticalAlign: -4 }} />React-Bootstrap<DiBootstrap size={20} style={{ display: "inline", verticalAlign: -4 }} />
                </span>
        </Navbar>
    </Styles>
)