import React from 'react';
import Card from 'react-bootstrap/Card';

const url = window.location.href;

export const NoMatch = () => (
    <div>
        <div class="container-fluid">
            <div class="row" align="center">
                <div class="col-12 pt-5">
                    <h1 class="display-1"><u><b>Error 404-</b></u></h1><br />
                    <h1 class="display-4">Wait... Ugh<br />What did you do this time...</h1><br />
                    <Card style={{ width: '24rem', backgroundColor: '#EAEAEF' }}>
                        <Card.Body>
                            You put: {url}
                        </Card.Body>
                    </Card>

                    <p>Code Error: 404<br />Missing Site Page. Report To Author If Error Continues</p>
                </div>
            </div>
        </div>
    </div>
)