import React from 'react';
import Card from 'react-bootstrap/Card';
import { ProgressBar, ListGroup } from 'react-bootstrap';
import 'bootstrap/dist/css/bootstrap.min.css';

export const About = () => (
    <div>
        <div class="container-fluid">
            <div class="row mx-auto pt-3">
                {/* Knowledge */}
                <div class="col-4">
                    <Card style={{ width: '21rem', height: '35rem' }}>
                        <Card.Body>
                            <Card.Title>Knowledge of Skills</Card.Title>
                                <ListGroup variant="flush">
                                    <ListGroup.Item>
                                    <Card.Subtitle className="mb-2 text-muted">Basic Languages</Card.Subtitle>
                                        HTML: <ProgressBar animated now={90} label={`90%`} />
                                        CSS: <ProgressBar animated now={70} label={`70%`} />
                                        JS: <ProgressBar animated now={70} label={`70%`} />
                                    </ListGroup.Item>
                                    <ListGroup.Item>
                                        <Card.Subtitle className="mb-2 text-muted">JavaScript Frameworks</Card.Subtitle>
                                        React: <ProgressBar animated now={40} label={`40%`} />
                                        Vue: <ProgressBar variant="warning" now={100} label={`Learning`} />
                                        Angular: <ProgressBar variant="info" now={100} label={`Not planned to learn`} />
                                        Svelte: <ProgressBar variant="success" now={100} label={`Want to learn`} />
                                    </ListGroup.Item>
                                    <ListGroup.Item>
                                        <Card.Subtitle className="mb-2 text-muted">CSS Frameworks</Card.Subtitle>
                                        Bootstrap: <ProgressBar animated now={70} label={`70%`} />
                                        Tailwind: <ProgressBar variant="warning" now={100} label={`Learning`} />
                                    </ListGroup.Item>
                                    <ListGroup.Item></ListGroup.Item>
                                </ListGroup>
                        </Card.Body>
                    </Card>
                </div>
                {/* About Site */}
                <div class="col-4">
                    <Card style={{ width: '21rem', height: '35rem' }}>
                        <Card.Body>
                            <Card.Title>Kxffie Hub Itself</Card.Title>
                            <ListGroup variant="flush">
                                <ListGroup.Item>
                                    <Card.Subtitle className="mb-1 text-muted">What it's made with</Card.Subtitle>
                                    This site was built on 08/08/2021 (mm-dd-yyyy), made with <a href="https://react-bootstrap.github.io/" target="_blank">React-Bootstrap</a>, <a href="https://styled-components.com/" target="_blank">Styled Components</a>, and <a href="https://reactrouter.com/" target="_blank">React Router</a>.
                                </ListGroup.Item>
                                <ListGroup.Item>
                                    <Card.Subtitle className="mb-1 text-muted">How it's made</Card.Subtitle>
                                    I made this entire site in less than a week. Actually, in less than a couple hours if you want to get technical. I coded this site by myself, with just a couple youtube videos to get me started.
                                </ListGroup.Item>
                                <ListGroup.Item>
                                    <Card.Subtitle className="mb-1 text-muted">Plans for the future</Card.Subtitle>
                                    I plan to remake this site a couple times, each with hopefully a new languages such as: Tailwind (or other CSS Frameworks), Angular/Vue (or other JS Frameworks) etc.
                                </ListGroup.Item>
                                <ListGroup.Item>
                                    
                                </ListGroup.Item>
                            </ListGroup>
                        </Card.Body>
                    </Card>
                </div>
                {/* About Me */}
                <div class="col-4">
                {/* About Me */}
                    <Card style={{ width: '21rem', height: '35rem' }}>
                        <Card.Body>
                            <Card.Title>Kxffie Himself</Card.Title>
                            <Card.Subtitle className="mb-2 text-muted">He/Him - 16 Y/O</Card.Subtitle>
                            <ListGroup variant="flush">
                                <ListGroup.Item>
                                    <Card.Subtitle className="mb-1 text-muted">Who I am</Card.Subtitle>
                                    Hey, I'm Kxffie, aka Bryce. Im a 16 year old web developer, obviously in highschool. I make websites/mini-games in my freetime for fun. Checkout the <a href="/projects">Projects</a> tab to see my past and currentprojects.
                                </ListGroup.Item>
                                <ListGroup.Item>
                                    <Card.Subtitle className="mb-1 text-muted">Name Explanation</Card.Subtitle>
                                    I chose the name Kxffie because my heritage in German. That doesn't make sense, so I'll tell you all the steps. My name relates to the word Coffee, but in Dutch. In German, Koffee (and Kxffee), are too popular names. In Dutch, it is Koffie, also popular but not as popular as Kxffie. Plus, I love coffee.
                                </ListGroup.Item>
                                <ListGroup.Item>
                                    
                                </ListGroup.Item>
                            </ListGroup>
                        </Card.Body>
                    </Card>
                </div>
            </div>
        </div>
    </div>
)